import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { NavigationExtras, Router } from '@angular/router';
import { ToastController } from '@ionic/angular';
import { ApiService } from 'src/app/services/api.service';
import { DataService } from 'src/app/services/data.service';
import { DeviceAccess } from 'src/app/services/device-access.service';

@Component({
  selector: 'app-setmpin',
  templateUrl: './setmpin.page.html',
  styleUrls: ['./setmpin.page.scss'],
})
export class SetmpinPage implements OnInit {
  isLoading: boolean = false;
  customerPhonenum: string;
  newMpin = new FormControl();
  mpinForm: FormGroup;
  mpin = new FormControl();
  mpinobj = new mpinObjects();

  config = {
    allowNumbersOnly: true,
    length: 4,
    isPasswordInput: false,
    disableAutoFocus: false,
    placeholder: '',
    inputStyles: {
      'width': '50px',
      'height': '50px'
    }
  };

  constructor(private router: Router,
    private location: Location,
    private api: ApiService,
    private fb: FormBuilder,
    public toastCtrl: ToastController,
    private device: DeviceAccess,
    private dataService: DataService) { }

  ngOnInit() {
    this.customerPhonenum = localStorage.getItem('customerPhonenum');

    this.mpinForm = this.fb.group({
      phoneNumber: ['', Validators.required],
      deviceId: ['', Validators.required],
      mpin: ['', Validators.required],
      confirmMpin: [],
    });
  }

  back() {
    this.location.back();
  }

  forgotmpin() {
    this.router.navigateByUrl('/forgotmpin');
  }

  async openToast(message) {
    const toast = await this.toastCtrl.create({
      message: `${message}`,
      duration: 2500,
      position: 'bottom',
    });
    toast.present();
  }


  async setMpin() {

    if (!this.newMpin.value || this.newMpin.value < 4 || !this.mpin.value || this.mpin.value.length < 4) {
      this.openToast('Enter valid mPin');
      return;
    }

    if (this.newMpin.value !== this.mpin.value) {
      this.openToast('New and current mPin did not match');
      return;
    }

    const deviceId = await this.device.getDeviceId();

    this.mpinobj.phoneNumber = this.customerPhonenum;
    this.mpinobj.mpin = this.mpin.value;
    this.mpinobj.deviceId = deviceId;

    if (this.newMpin.value !== this.mpin.value) {
      this.openToast('mPin did not match');
      return;
    }

    this.api
      .setMpin(this.mpinobj)
      .subscribe((res: any) => {
        if (res.status == 200) {
          localStorage.setItem('deviceId', deviceId);
          localStorage.setItem(
            'customer_details',
            JSON.stringify(res.data)
          );
          this.dataService.isLoggedIn.next(true);
          const navigationExtras: NavigationExtras = {
            queryParams: {
              'screenDetails': 'Set Login mPIN!',
              'screenDescription': 'Your mPIN has been registered successfully',
              'screenName': 'setmpin'
            },
          };
          this.api.sendNavParam(navigationExtras);
          this.router.navigateByUrl('/success-message');
        } else {
          this.openToast(res.message);
        }
      }, (error: any) => {
        this.isLoading = false;
      });
  }

}
export class mpinObjects {
  phoneNumber: any;
  mpin: any;
  deviceId: any;
  currentMpin?: any
}